@-webkit-keyframes tesiMove {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  15% {
    -webkit-transform: rotate(2deg);
    transform: rotate(2deg);
  }
  33% {
    -webkit-transform: rotate(4deg);
    transform: rotate(4deg);
  }
  66% {
    -webkit-transform: rotate(2deg);
    transform: rotate(2deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
}
@keyframes tesiMove {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  15% {
    -webkit-transform: rotate(2deg);
    transform: rotate(2deg);
  }
  33% {
    -webkit-transform: rotate(4deg);
    transform: rotate(4deg);
  }
  66% {
    -webkit-transform: rotate(2deg);
    transform: rotate(2deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
}
